.createGame-cnt {
    background-color: #363636;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    color: #F2F2F2;
    padding-top: 4vh;
    padding-bottom: 4vh;
}

.createGame-cnt-title {
    /* background-color: #a945c71c; */
    width: 90%;
    height: auto;
    margin: .6em auto;
    padding: .5em 0;
}

.createGame-cnt-title h1 {
    font-size: 2em;
}

.createGame-cnt form {
    /* background-color: rgba(255, 255, 255, 0.049); */
    box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.108);
    width: 90%;
    height: auto;
    margin: 0 auto;
    color: #FFE600;
    font-weight: bolder;
}

.form-name {
    /* background-color: rgba(255, 0, 0, 0.276); */
    height: auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: .8em .4em;
}

.form-name label {
    font-size: 1.2em;
    display: inline-block;
}

.form-name input {
    width: 100%;
    margin: .2em auto;
    height: 2.5em;
    border: none;
    outline: none;
    border-radius: 16px;
    box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.337);
    padding: .5em;
}

.form-name p {
    color: rgb(190, 0, 0);
    font-size: 1.2em;
    font-weight: bold;
}

.form-released {
    /* background-color: rgba(60, 255, 0, 0.276); */
    height: auto;
