}

.select-opt {
    /* background-color: #FFE600; */
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.genres-opts {
    /* background-color: red; */
    background-color: #0058DD;
    width: 48%;
    height: auto;
    margin: .5em 0;
    box-shadow: 1px 2px 5px .5px #9b95684f;
    color: #F2F2F2;
    padding: 0;
    display: flex;
}




.genres-opts button {
    /* background-color: #F2F2F2; */
    width: 25%;
    min-height: 3em;
    border: none;
    cursor: pointer;
    font-weight: bold;
}

.genres-opts span {
    /* background-color: #26a495; */
    width:75%;
    min-height: 2.5em;
    padding: .5em;
}

.form-buttons {
    height: auto;
    width: 100%;
    margin: .6em 0;
    display: flex;
    flex-direction: column;
    padding: .8em .4em;
}

/* 800px para adelante */
@media (min-width: 800px) {
    .form-buttons {
        /* background-color: aquamarine; */
        height: auto;
        width: 100%;
        margin: .6em 0;
        display: flex;
        justify-content: center;
        flex-direction: row;
        padding: 1em .4em;
    }
}

/* 1024px para adelante */
@media (min-width: 1024px) {
    .createGame-cnt form {
        background-color: #1c66d453;
        box-shadow: 2px 2px 4px 2px rgba(0, 0, 0, 0.108);
        width: 60%;
        height: auto;
        margin: 1em auto;
        padding: 1em;
        border-radius: 21px;
        color: #FFE600;
        font-weight: bolder;
    }
}