.search-cnt {
    background-color: #0058DD;
    padding: .2em 0;
}

.form-search-cnt {
    background-color: rgba(0, 0, 0, 0.362);
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 10px 20px;
    backdrop-filter: blur(4px) saturate(180%);
}

.form-search-cnt input {
    background-color: transparent;
    flex: 1;
    border: 0;
    outline: none;
    padding: 15px 11px;
    font-size: 1em;
    color: #fff;
}

::placeholder {
    color: rgba(255, 255, 255, 0.558);
}

.form-search-cnt button {
    border: 0;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    background-color: rgba(0, 0, 0, 0.697);
    cursor: pointer;
    transition: .6s;
}

.form-search-cnt button img {
    width: 25px;
}

.form-search-cnt button:hover {
    background-color: rgba(0, 0, 0, 0.827);
    transition: .6s;
    
}

